<template>
	<Tabs v-model="tab" class="tab" @on-click="toggleTab">
		<Tab-pane label="入库申请" name="in" class="tab-item">
			<div class="search-box" @keyup.enter="search">
				<div class="search-item name">
					<Input v-model="inTab.searchParams.aBaseName" size="large" style="width: 260px"
						   placeholder="请输入产权人姓名">
					<span slot="prepend" class="input-title">产权人</span>
					</Input>
				</div>
				<div class="search-item orderNum">
					<Input v-model="inTab.searchParams.loanId" size="large" style="width: 260px" placeholder="请输入贷款编号">
						<span slot="prepend" class="input-title">贷款编号</span>
					</Input>
				</div>
				<div class="search-item">
					<i-button type="primary" size="large" class="search" :disabled="refreshFlag" @click="search">查询</i-button>
					<i-button type="success" size="large" class="search" @click="clearAll">清空</i-button>
				</div>
			</div>
			<Table size="large" highlight-row :columns="inTab.inHeader" :data="inTab.inList"></Table>
			<div class="page">
				<Page placement='top' :total="inTab.totalPage" :current="inTab.searchParams.currentPage"
					  :page-size="inTab.searchParams.pageSize"
					  :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<Tab-pane label="历史记录" name="history" class="tab-item">
			<div class="search-box" @keyup.enter="search">
				<div class="search-item name">
					<Input v-model="historyTab.searchParams.aBaseName" size="large" style="width: 260px"
						   placeholder="请输入产权人姓名">
					<span slot="prepend" class="input-title">产权人</span>
					</Input>
				</div>
				<div class="search-item orderNum">
					<Input v-model="historyTab.searchParams.loanId" size="large" style="width: 260px"
						   placeholder="请输入贷款编号">
					<span slot="prepend" class="input-title">贷款编号</span>
					</Input>
				</div>
				<div class="search-item">
					<div class="item-title color-green">入库时间</div>
					<Date-picker size="large" type="daterange" :editable="false" placement="bottom-end"
								 format="yyyy-MM-dd" placeholder="选择审核时间范围--年/月/日" style="width:352px"
								 v-model="historyTab.dateRange" @on-change="dateChange"
					></Date-picker>
				</div>
				<div class="search-item">
					<i-button type="primary" size="large" class="search" :disabled="refreshFlag" @click="search">查询</i-button>
					<i-button type="success" size="large" class="search" @click="clearAll">清空</i-button>
				</div>
			</div>
			<Table size="large" highlight-row :columns="historyTab.historyHeader" :data="historyTab.historyList"></Table>
			<div class="page">
				<Page placement='top' :total="historyTab.totalPage" :current="historyTab.searchParams.currentPage"
					  :page-size="historyTab.searchParams.pageSize" :page-size-opts="[10,20,30,40]"
					  show-elevator show-sizer show-total
					  @on-change="pageChange" @on-page-size-change="pageSizeChange"
				></Page>
			</div>
		</Tab-pane>
		<i-button type="text" slot="extra" class="extra-btn" icon="ios-loop-strong" :disabled="refreshFlag" @click="refresh">刷新</i-button>
	</Tabs>
</template>
<script>
	export default {
		data () {
			return {
				inTab: {
					inHeader: [
						{
							type: 'index',
							title: '序号',
							width: '80px',
							align: 'center'
						},
						{
							title: '押品编号',
							key: 'guaranteeNo',
							align: 'center'
						},
						{
							title: '贷款编号',
							key: 'loanId',
							align: 'center',
							render: (h, params) => {
								return h('router-link', {
									props: {
										to: {name: 'pawninfo', params: {loanId: params.row.loanId,serialNo:params.row.serialNo}},
									},
								}, params.row.loanId)
							}
						},
						{
							title: '贷款客户',
							key: 'realName',
							align: 'center'
						},
						{
							title: '账务确认时间',
							key: 'finalTransferDate',
							align: 'center',
							render: (h, params) => {
								return h('span', new Date(params.row.finalTransferDate).Format("yyyy-MM-dd"))
							}
						},
						{
							title: '押品类型',
							key: 'gmType',
							align: 'center'
						},
						{
							title: '评估金额',
							key: 'mhouseValue',
							align: 'center',
							render: (h, params) => {
								let _this = this;
								return h('span', _this.fmoney(params.row.mhouseValue))
							}
						},
						{
							title: '产权人',
							key: 'propertyOwner',
							align: 'center'
						},
						{
							title: '押品状态',
							key: 'gmStatus',
							align: 'center',
							render: (h, params) => {
								if (params.row.gmStatus == '0') {
									return h('span', {
										style: {
											color: '#19be6b'
										},
									}, '待申请')
								} else if (params.row.gmStatus == '3') {
									return h('span', {
										style: {
											color: '#ff9900'
										},
									}, '审核未通过')
								}
							}
						},
						{
							title: '操作',
							key: 'action',
							align: 'center',
							width: 100,
							render: (h, params) => {
								return h('i-button', {
									props: {
										type: params.row.gmStatus == '0' ? 'success' : 'warning',
										size: 'small',
									},
									on: {
										click: () => {
											this.$router.push({
												name: 'applyin',
												params: {
													loanId: params.row.loanId,
													serialNo:params.row.serialNo
												}
											})
										}
									}
								}, params.row.gmStatus == '0' ? '申请入库' : '重新申请')
							}
						}
					],
					inList: [],
					totalPage: 10,
					searchParams: {
						aBaseName: '',
						loanId: '',
						currentPage: 1,
						pageSize: 10
					}
				},
				historyTab: {
					historyHeader: [
						{
							type: 'index',
							title: '序号',
							width: '80px',
							align: 'center'
						},
						{
							title: '押品编号',
							key: 'guaranteeNo',
							align: 'center'
						},
						{
							title: '关联贷款编号',
							key: 'loanId',
							align: 'center',
							render: (h, params) => {
								return h('router-link', {
									props: {
										to: {name: 'pawninfo', params: {loanId: params.row.loanId,serialNo:params.row.serialNo}},
									},
								}, params.row.loanId)
							}
						},
						{
							title: '贷款客户',
							key: 'realName',
							align: 'center'
						},
						{
							title: '账务确认时间',
							key: 'finalTransferDate',
							align: 'center',
							render: (h, params) => {
								return h('span', new Date(params.row.finalTransferDate).Format("yyyy-MM-dd"))
							}
						},
						{
							title: '押品入库时间',
							key: 'gmInTime',
							align: 'center',
							render: (h, params) => {
								return h('span',params.row.gmInTime?new Date(params.row.gmInTime).Format("yyyy-MM-dd"):'-')
							}
						},
						{
							title: '押品类型',
							key: 'gmType',
							align: 'center'
						},
						{
							title: '评估金额',
							key: 'mhouseValue',
							align: 'center',
							render: (h, params) => {
								let _this = this;
								return h('span', _this.fmoney(params.row.mhouseValue))
							}
						},
						{
							title: '产权人',
							key: 'propertyOwner',
							align: 'center'
						},
						{
							title: '押品状态',
							key: 'gmStatus',
							align: 'center',
							render: (h, params) => {
								let type=params.row.gmStatus;
								if (type == '1') {
									return h('span', {
										style: {
											color: '#ed3f14'
										}
									}, '审核中')
								} else if (type >= '2' && type != '3') {
									return h('span', {
										style: {
											color: '#19be6b'
										}
									}, '审核通过')
								}else if(type == '3') {
									return h('span', {
										style: {
											color: '#ff9900'
										}
									}, '审核未通过')
								}else {
									return h('span', {
										style: {
											color: '#ed3f14'
										}
									}, '未知状态')
								}
							}
						}
					],
					historyList: [],
					dateRange: [],
					totalPage: 10,
					searchParams: {
						aBaseName: '',
						loanId: '',
						startTime: '',
						endTime: '',
						currentPage: 1,
						pageSize: 10
					}
				},
				tab: 'in',
				refreshFlag:true
			}
		},
		created: function () {
			this.loadList();
		},
		methods: {
			pageChange: function (val) {
				let _this = this;
				if (_this.tab == 'in') {//待审核切换页码
					if (_this.inTab.searchParams.currentPage != val) {
						_this.inTab.searchParams.currentPage = val;
						_this.loadList();
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.currentPage != val) {
						_this.historyTab.searchParams.currentPage = val;
						_this.loadList();
					}
				}
			},
			pageSizeChange: function (val) {
				let _this = this;
				if (_this.tab == 'in') {//待审核切换
					if (_this.inTab.searchParams.pageSize != val) {
						_this.inTab.searchParams.pageSize = val;
						_this.loadList();
					}
				} else if (_this.tab == 'history') {//历史记录
					if (_this.historyTab.searchParams.pageSize != val) {
						_this.historyTab.searchParams.pageSize = val;
						_this.loadList();
					}
				}
			},
			toggleTab: function (name) {
				this.refreshFlag=true;
				this.loadList();
			},
			loadList: function () {
				let _this = this;
				_this.$Message.config({
					top: 200,
					duration: 2
				});
				clearTimeout(timer);
				_this.$Message.destroy();
				let timer = setTimeout(function () {
					_this.$Message.loading({
						content: '数据请求中...',
						duration: 0
					});
				}, 500);
				if (_this.tab == 'in') {//待审核
					_this.$http.post('/Storage/passEnterStorage', _this.inTab.searchParams)
						.then(function (response) {
							var res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.inTab.totalPage = res.data.count;
									_this.inTab.searchParams.currentPage = res.data.currentPage;
									_this.inTab.searchParams.pageSize = res.data.pageSize;
									_this.inTab.inList = res.data.list;
								}else {
									_this.inTab.inList = [];
								}
							} else {
								_this.$Message.error({
									content: res.msg
								});
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							});
						});
				} else if (_this.tab == 'history') {//历史记录
					this.$http.post('/Storage/outEnterStorage', _this.historyTab.searchParams)
						.then(function (response) {
							let res = response.data;
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							if (res.code == 1) {//正常
								_this.$Message.success({
									content: res.msg
								});
								if(res.data.list){
									_this.historyTab.totalPage = res.data.count;
									_this.historyTab.searchParams.currentPage = res.data.currentPage;
									_this.historyTab.searchParams.pageSize = res.data.pageSize;
									_this.historyTab.historyList = res.data.list;
								}else {
									_this.historyTab.historyList = [];
								}
							} else {
								_this.$Message.error(res.msg);
							}
						})
						.catch(function (error) {
							clearTimeout(timer);
							_this.$Message.destroy();
							_this.refreshFlag=false;
							_this.$Message.error({
								content: error
							})
						});
				}
			},
			dateChange: function (range) {
				let _this = this;
				if (_.includes(range, undefined)) {
					_this.historyTab.searchParams.startTime = '';
					_this.historyTab.searchParams.endTime = '';
				} else {
					_this.historyTab.searchParams.startTime = range[0];
					_this.historyTab.searchParams.endTime = range[1];
				}
			},
			search: function () {
				let _this = this;
				//清空页码
				if (_this.tab == 'in') {
					_this.inTab.searchParams.currentPage = 1;
					_this.inTab.searchParams.pageSize = 10;
				} else if (_this.tab == 'history') {
					_this.historyTab.searchParams.currentPage = 1;
					_this.historyTab.searchParams.pageSize = 10;
				}
				//搜索
				_this.refreshFlag=true;
				_this.loadList();
			},
			refresh: function () {
				this.refreshFlag=true;
				this.loadList();
			},
			clearAll: function () {//清空搜索，翻页等信息
				this.inTab.searchParams.aBaseName = '';
				this.inTab.searchParams.loanId = '';
				this.historyTab.dateRange = [];
				this.historyTab.searchParams.aBaseName = '';
				this.historyTab.searchParams.loanId = '';
				this.historyTab.searchParams.startTime = '';
				this.historyTab.searchParams.endTime = '';
				this.historyTab.searchParams.currentPage = 1;
				this.historyTab.searchParams.pageSize = 10;
			}
		}
	}
</script>
<style>
	.tab {
		padding-top: 15px;
	}

	.ivu-tabs-tab {
		font-size: 16px;
	}

	.page {
		padding-top: 50px;
		display: flex;
		justify-content: flex-end;
		flex-direction: row;
		padding-bottom: 50px;
	}

	.layout-content .extra-btn {
		color: #20abff;
		font-size: 14px;
	}

	.name .ivu-input-group-prepend {
		background-color: #20abff;
		border-color: #20abff;
		width: 80px;
	}

	.orderNum .ivu-input-group-prepend {
		background-color: #f0b320;
		border-color: #f0b320;
		width: 80px;
	}

	.input-title {
		color: #fff;
		font-size: 14px;
	}

	.search-box {
		display: flex;
		flex-direction: row;
		justify-content: space-around;
		align-items: center;
		padding-top: 14px;
		padding-bottom: 15px;
		flex-wrap: wrap;
	}

	.search-item {
		flex: 1;
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		padding-bottom: 15px;
		margin-right: 10px;
	}

	.item-title {
		width: 80px;
		height: 36px;
		font-size: 14px;
		line-height: 36px;
		text-align: center;
		color: #fff;
		border-top-left-radius: 4px;
		border-bottom-left-radius: 4px;
	}

	.color-yellow {
		background-color: #f0b320;
	}

	.color-red {
		background-color: #ff7043;
	}

	.color-green {
		background-color: #28b779;
	}

	.search {
		width: 100px;
		margin-right: 10px;
	}

	.ivu-select-selection {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}

	.search-item .ivu-input {
		border-top-left-radius: 0px;
		border-bottom-left-radius: 0px;
	}
</style>



